* {
	box-sizing: border-box;
}

body {
	background-size: cover;
	font-family: poppins;
}

.app-wrap {
	background-color: white;
	width: 700px;
	height: auto;
	border-radius: 15px;
	margin: 0 auto;	
	text-align: center;
	background-color: #f4f4f4;
	padding: 5px 0 20px 0;
}

h1 {
	background-color: #f4f4f4;
	color: #191970;
	font-size: 45px;
	text-align: center;
	padding: 0 34px 0 34px;
}

.delbtn {
	margin-left: 20px;
	color: white;
  width: 100px;
  height: 30px;
  border: none;
  border-radius: 3px;
  background-color: red;
  font-weight: bold;
  text-align: center;
  font-family: poppins;
}

.editbtn {
	color: white;
  width: 100px;
  height: 30px;
  border: none;
  border-radius: 3px;
  background-color: #191970;
  font-weight: bold;
  text-align: center;
  font-family: poppins;
}

li {
	text-align: left;
	font-weight: bold;
	list-style-type: none;
	padding-top: 10px;

}

input[type=text] {
	display: block;
 	width: 250px;
  	height: 35px;
  	border: none;
	border-bottom: 2px solid #191970;
  	color: #191970;
  	padding-left: 15px;
  	margin-bottom: 20px;
  	margin-left: 20px;
  	background-color: #f4f4f4;
 	font-weight: 400;
 	font-size: 18px;
 	justify-content: center;
}

.add-task-btn {
  margin-left: 20px;
  width: 150px;
  height: 35px;
  border: none;
  border-radius: 3px;
  background-color: #191970;
  color: white;
  font-weight: bold;
  text-align: center;
  font-family: poppins;
}


::placeholder {
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: center;
  top: 17px;
  left: 20px;
}

.task-div {
	background: #f4f4f4;
}

.task-div:nth-child(odd) {
		background: white;
}